
/*列表页面*/
.vux_noMarginTop .weui-cells{
	margin-top: 0px!important;
	padding-bottom: 15px;
	background-color: #f1f1f1;
}
.vux_noMarginTop:last-of-type .weui-cells {
	padding-bottom: 0px;
}
.vux_noMarginTop .weui-cells .weui-cell {
	background-color: #fff;
}
.vux_noMarginTop .weui-cells .weui-cell .vux-cell-bd P {
	font-size: 14px;
}
.vx_jh_search {
	width: 100%;
	position: absolute;
	top: 0px;
}
.vx_jh_search.vux_noMarginTop .weui-cells {
	padding-bottom: 0px;
}
/* 功能菜单*/
.weui-grid .weui-grid__icon {
	width: 34px;
	height: 34px;
}
.vux_noitem .weui-grid__label {
	height: 22px;
}
.vux_noitem .weui-grid__icon img {
	visibility: hidden;
}
/*表单界面*/
.vux_noFromMarginTop .weui-cells,.vux_noSonFromMarginTop .weui-cells{
	margin-top: 0px!important;
}
.vux_noFromMarginTop .weui-cells:before,.vux_noSonFromMarginTop .weui-cells:after {
	border-color: #f1f1f1;
}
.vux_noFromMarginTop .weui-cells:after,.vux_noSonFromMarginTop .weui-cells:before,.vux_noSonFromMarginTop .vux-cell-box:before,.vux_jh_selTime .vux-datetime.weui-cell:before, .vux_jh_from .vux_jh_input.weui-cell:before,.vux_noFromMarginTop .weui-cells .vux-cell-box:before {
	border: 0px!important;
}
.vux_jh_from:last-of-type .weui-cells:after {
	border: 0px;
}
.vux_jh_from .weui-cells{
	width: 100%;
	display: flex;
	display: -webkit-flex;
	flex-direction: row;
}
.vux_jh_from .vux_jh_icon {
	flex: 0 0 10%;
	background-color: #fff;
}
.vux_jh_from .vux_jh_icon img {
	width: 20px;
	height: 20px;
	position: relative;
	left: 14px;
	top: 14px;
}
.vux_jh_from .vux_jh_input {
	flex: 0 0 90%;
	box-sizing: border-box;
}
.vux_jh_from .vux_jh_input.weui-cell:before {
	border: 0px;
}
.vux_jh_from .vux_jh_input .vux-popup-picker-select-box .vux-popup-picker-select span {
	font-size: 14px;
	color: #999;
}
.vux_jh_from .vux_jh_input.black .vux-popup-picker-select span,.vux_jh_twoInput .vux_jh_rightInput.black .vux-popup-picker-select span,.vux_jh_from .vux_jh_input.black .vux-datetime-value {
	color: #333;
}

.spliteLine {
	margin: 10px 0;
	height: 1px;
	border-top: 1px solid #ccc;
}
.vux_jh_input .weui-cell__bd{
	font-size: 14px;
}
.vux_jh_twoInput .weui-cells{
	width: 100%;
	display: flex;
	display: -webkit-flex;
}
.vux_jh_twoInput .vux_jh_leftInput {
	flex: 0 0 65%;
	box-sizing: border-box;
}
.vux_jh_twoInput .weui-cell:before {
	border-top: 0px;
}
.vux_jh_twoInput .vux_jh_rightInput {
	flex: 0 0 35%;
	box-sizing: border-box;
	border-left: 1px solid #f1f1f1;
}
.vux_jh_twoInput .vux_jh_rightInput .weui-cell .vux-popup-picker-select span {
	font-size: 14px;
	color: #999;
}
.vux_jh_twoInput .vux_jh_rightInput.black .weui-cell .vux-popup-picker-select span {
	color: #333;
}
.vux_jh_selTime .vux-datetime .vux-datetime-value {
	font-size: 14px;
	color: #999;
	text-align: left;
}
.vux_jh_selTime .weui-cells .vux_jh_icon img {
	left: 16px;
	top: 9px;
}
.vux_jh_textArea .weui-cells .vux_jh_icon img {
	top: 9px;
}
.vux_jh_unit {
	color: rgb(18, 150, 219);
    font-size: 14px;
    display: inline-block;
    width: 20px;
    height: 20px;
    border: 1px solid rgb(18, 150, 219);
    border-radius: 100%;
    text-align: center;
}




